.backgroundVideo{
    display: flex;
    .overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.3);
    }
    video{
        height: 100vh;
        width: 100%;
        object-fit: cover;
    }
}