@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100&family=Montserrat:ital,wght@0,200;0,300;0,400;1,200&family=Oswald:wght@300;400&family=Poiret+One&family=Poppins:ital,wght@0,600;0,700;1,200&family=Roboto:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inspiration&family=Montserrat:ital,wght@0,200;0,300;0,400;1,200&family=Oswald:wght@300;400&family=Poppins:ital,wght@0,600;0,700;1,200&family=Roboto:wght@100&display=swap');
.text{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
}
.logo-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    padding:  0 40px;   
    h2{
        font-family: 'Inspiration';
        letter-spacing: 7px;
        font-size: 35px;
        color: #fff;
    }
    h3{
        font-family: 'Josefin Sans';
        color: #fff;
        margin-top: 10px;
    }
    div{
        margin-top: 20px;
        background-color: rgba(41,41,41,.4);
        color: #ddd;
        letter-spacing: 2px;
        padding: 15px 50px;
        display: flex;
        opacity: 0.5;
        border-radius: 5px;
        transition: 0.5s;
        &:hover{
            cursor: pointer;
            background-color: rgba(27, 27, 27, 0.4);
            opacity: 0.8;
            transition: 0.5s;

        }
        .globe{

        }
        span{
            margin: 0 20px;
            font-size: 13px;
        }
        .arrow{
            margin-left: 15px;
        }
    }

}

.mens{
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    button{
        padding: 15px 142px;
        border-radius: 5px;
        margin: 10px 0;
        background: transparent;
        border: 1px solid #fff;
        cursor: pointer;
        color: #fff;
        transition: 0.5s;
        letter-spacing: 5px;
        &:hover{
            opacity: 0.6;
            transition: 0.5s;
        }
    }
}

.cookie{
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    font-size: 14px;
    cursor: pointer;
}

.sidebar{
    position: absolute;
    right: 0;
    top: 0;
    width: 460px;
    height: 100%;
    background: #fff;
    display: flex;
    flex-direction: column;
    padding: 40px 20px;
    visibility: hidden;
    opacity: 0;
    transition: 0.5s;
        &.show{
            transition: 0.5s;
            visibility: visible;
            opacity: 1;
        }
    .xMark{
        display: flex;
        justify-content: space-between;
        font-size: 15px;
        img{
            position: absolute;
            right: 20px;
            top: 25px;
            width: 15px;
            height: 15px;
            cursor: pointer;
        }
    }
    
    .block-input{
        display: flex;
        align-items: center;
        border: 1px solid #e9e5e5;
        margin-top: 25px;
       
        &:active{
            border: 1px solid #000;
        }
        input{
            width: 100%;
            padding: 10px;
            border: none;
            outline: none;
            letter-spacing: 2px;
        }
        img{
            width: 15px;
            height: 15px;
            margin-left: 20px;
            cursor: pointer;
        }
    }

    h4{
        margin-top: 25px;
        font-size: 13px;
    }

    .list{
        ul{
            margin-top: 20px;
            li{
                padding: 25px 10px;
                display: flex;
                justify-content: space-between;
                list-style: none;
                cursor: pointer;
                border-bottom: 1px solid #dad7d7;
                text-transform: capitalize;
                font-size: 12px;
                transition: 0.5s;
                &:hover{
                    border-bottom: 1px solid #000;
                    transition: 0.5s;

                }
                span{
                    img{
                        width: 12px;
                    }
                }
                div{
                    position: absolute;
                    margin-top: 37px;
                    width: 410px;
                    height: 450px;
                    background: #ddd;
                    border-radius: 5px;
                    .little_list{
                        li{
                            border: none;
                            margin-left: 30px;
                            &:hover{
                                border-bottom: 1px solid #000;
                            }
                        }
                    }
                }
            }
        }
    }
}




